// -----------------------------------------------------------------------------
// Button
// -----------------------------------------------------------------------------

.button {
    background-color: $base-color;
    color: $color-white;
    padding: 5px 30px;
    border-radius: 5px;
    font-size: 14px;

    &:focus {
        outline: 0;
        box-shadow: none;
    }
}

.button--delete {
    background-color: $color-red;
}

.button--cancel {
    background-color: $color-medium-grey;
    color: $color-light-grey;
}