/***** Top menu *****/
 
.navbar {
    background: $base-color;
    -o-transition: all .6s;
    -moz-transition: all .6s;
    -webkit-transition: all .6s;
    -ms-transition: all .6s;
    transition: all .6s;
    backface-visibility: hidden;
   

    &.navbar-no-bg {
        background: #444;
        background: rgba(0, 0, 0, 0.2);
    }

    &.navbar-dark {
        .navbar-nav {
            font-size: 15px;
            color: #fff;
            text-transform: capitalize;

            .nav-item {
                padding: 0 15px;

                a {
                    &.trial {
                        background-color: $color-tosca;
                        color: $base-color;
                        border-radius: 50px;
                        padding: 5px 20px;
                    }
                }
            }
        }

        .navbar-link {
            color: #fff;
            color: rgba(255, 255, 255, 0.8);
            border: 0;

            &:hover {
                color: #fff;
            }

            &:focus {
                color: #fff;
                outline: 0;
            }
        }
    }

    &.changeups {
        padding: 15px 0px;
        transition: all .6s;
    }
}
  
.navbar-expand-md .navbar-nav .nav-link {
    padding-left: 1rem;
    padding-right: 1rem;
}
 
.navbar-brand {
    width: 190px;
}