.button {
  background-color: #2D2DAD;
  color: #ffffff;
  padding: 5px 30px;
  border-radius: 5px;
  font-size: 14px;
}

.button:focus {
  outline: 0;
  box-shadow: none;
}

.button--delete {
  background-color: #D14739;
}

.button--cancel {
  background-color: #DBDBDB;
  color: #636363;
}

/***** Top menu *****/
.navbar {
  background: #2D2DAD;
  -o-transition: all .6s;
  -moz-transition: all .6s;
  -webkit-transition: all .6s;
  -ms-transition: all .6s;
  transition: all .6s;
  backface-visibility: hidden;
}

.navbar.navbar-no-bg {
  background: #444;
  background: rgba(0, 0, 0, 0.2);
}

.navbar.navbar-dark .navbar-nav {
  font-size: 15px;
  color: #fff;
  text-transform: capitalize;
}

.navbar.navbar-dark .navbar-nav .nav-item {
  padding: 0 15px;
}

.navbar.navbar-dark .navbar-nav .nav-item a.trial {
  background-color: #68E2D8;
  color: #2D2DAD;
  border-radius: 50px;
  padding: 5px 20px;
}

.navbar.navbar-dark .navbar-link {
  color: #fff;
  color: rgba(255, 255, 255, 0.8);
  border: 0;
}

.navbar.navbar-dark .navbar-link:hover {
  color: #fff;
}

.navbar.navbar-dark .navbar-link:focus {
  color: #fff;
  outline: 0;
}

.navbar.changeups {
  padding: 15px 0px;
  transition: all .6s;
}

.navbar-expand-md .navbar-nav .nav-link {
  padding-left: 1rem;
  padding-right: 1rem;
}

.navbar-brand {
  width: 190px;
}

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  height: 100%;
  font-size: 62.5%;
}

body {
  font-family: 'Encode Sans Expanded', serif;
  font-size: 16px;
  line-height: 30px;
  background: #ffffff;
  color: #4C4C4C;
  letter-spacing: normal;
  font-weight: 400;
}

body .page {
  background: #F7F7F7;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Encode Sans Expanded', serif;
  font-weight: 700;
}

h1 {
  font-size: 44px;
}

h2 {
  font-size: 38px;
}

h3 {
  font-size: 26px;
}

a {
  color: #2D2DAD;
}

.section-item-padding {
  padding-bottom: 120px;
}

.hero-banner .row {
  min-height: 100vh;
}

.hero-banner .hero-content {
  width: 80%;
  margin: 0 auto;
}

.hero-banner .hero-content h1 {
  color: #2D2DAD;
  margin-bottom: 30px;
}

.hero-banner .hero-content p {
  margin-bottom: 40px;
}

.hero-banner .hero-content a {
  background-color: #68E2D8;
  color: #2D2DAD;
  border-radius: 50px;
  padding: 10px 20px;
  font-weight: 500;
}

.hero-banner .hero-content a:hover {
  cursor: pointer;
}

.section-title,
.section-item-title {
  color: #2D2DAD;
  margin-bottom: 20px;
}

.features > .container {
  border-top: 1px solid #eaeaea;
  padding-top: 40px;
}

.features .featureitem {
  padding-bottom: 120px;
  position: relative;
}

.features .featureitem.auto-store:after {
  position: absolute;
  content: '';
  left: -23%;
  top: 25%;
  background: url(../img/Ellipse81.png) no-repeat;
  background-position: bottom left;
  width: 50%;
  height: 50%;
  z-index: -1;
  background-size: contain;
}

.features .featureitem.invenmanage:after {
  position: absolute;
  content: '';
  right: -10%;
  top: 25%;
  background: url(../img/Polygon8.png) no-repeat;
  background-position: bottom right;
  width: 60%;
  height: 60%;
  z-index: -1;
  background-size: contain;
}

.features .featureitem.store-integrated:after {
  position: absolute;
  content: '';
  left: -12%;
  top: 25%;
  background: url(../img/Rectangle621.png) no-repeat;
  background-position: bottom left;
  width: 40%;
  height: 40%;
  z-index: -1;
  background-size: contain;
}

.pricing > .container {
  border-top: 1px solid #eaeaea;
  padding-top: 40px;
}

.pricing .pricebox {
  text-align: center;
  padding: 20px;
  border-radius: 20px;
  min-height: 410px;
  margin-bottom: 15px;
}

.pricing .pricebox ul {
  list-style: none;
  padding-left: 0px;
}

.pricing .pricebox h5 {
  margin-bottom: 20px;
  font-weight: 600;
}

.pricing .pricebox .prcheading {
  margin-bottom: 20px;
  min-height: 54px;
}

.pricing .pricebox .prcheading h3 {
  font-weight: 600;
}

.pricing .pricebox.starter {
  background-color: #2D2DAD;
  color: #ffffff;
}

.pricing .pricebox.starter h5 {
  color: #68E2D8;
}

.pricing .pricebox.starter h3 {
  color: #ffffff;
  text-transform: uppercase;
}

.pricing .pricebox.business {
  background-color: #68E2D8;
}

.pricing .pricebox.business h5 {
  color: #2D2DAD;
}

.pricing .pricebox.enterprise {
  background-color: #ffffff;
  border: 2px solid #2D2DAD;
}

.pricing .pricebox.enterprise h5 {
  color: #68E2D8;
}

.pricing .pricebox.enterprise h3 {
  color: #2D2DAD;
}

.pricing .priceaction {
  text-align: center;
  margin-bottom: 30px;
}

.pricing .priceaction p {
  margin-bottom: 0;
  display: inline-block;
  padding: 10px 20px;
  border-radius: 25px;
}

.pricing .priceaction.current p {
  border: 1px solid #2D2DAD;
  background-color: #ffffff;
}

.pricing .priceaction.base p {
  border: 1px solid #2D2DAD;
  background-color: #2D2DAD;
  color: #ffffff;
}

footer {
  background-color: #2D2DAD;
  color: #ffffff;
}

footer .footer-wrapper {
  padding: 50px 0;
}

footer .footer-wrapper .widget-imgfoot {
  margin-bottom: 30px;
}

footer .footer-wrapper .widget-imgfoot img {
  width: 180px;
}

footer .footer-wrapper .footer-social {
  padding-left: 0px;
  margin-top: 30px;
}

footer .footer-wrapper .footer-social li {
  list-style: none;
  padding-left: 0;
  display: inline-block;
  margin-right: 10px;
}

footer .footer-wrapper .footer-social li a {
  display: block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  border-radius: 50%;
  background-color: #ffffff;
  color: #2D2DAD;
  font-size: 14px;
  -webkit-transition: all all 0.5s ease-out 0s;
  -moz-transition: all all 0.5s ease-out 0s;
  -ms-transition: all all 0.5s ease-out 0s;
  -o-transition: all all 0.5s ease-out 0s;
  transition: all all 0.5s ease-out 0s;
}

footer .footer-wrapper .footer-social li a:hover {
  background-color: #68E2D8;
}

footer .footer-wrapper .widget-copyright {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: flex-end;
  height: 100%;
}

@media screen and (min-width: 992px) {
  .hero-banner {
    margin-top: 40px;
  }
  .hero-banner .hero-image img {
    max-width: 110%;
  }
}

@media screen and (min-width: 768px) {
  .navbar {
    padding: 40px 0;
  }
}

@media screen and (max-width: 768px) {
  body {
    font-size: 12px;
    line-height: 26px;
  }
  h2 {
    font-size: 30px;
  }
  .navbar.fixed-top {
    position: relative;
  }
  .prcinfo {
    font-size: 14px;
  }
  .hero-banner .hero-content h1 {
    font-size: 32px;
  }
  .section-item-padding {
    padding-bottom: 60px;
  }
}
