@media screen and (min-width: 992px) {
    .hero-banner {
        margin-top: 40px;
        
        .hero-image {
          img {
                max-width: 110%;
            }
        }
    }
}
@media screen and (min-width: 768px) {
    .navbar {
        padding: 40px 0;
    }
}
@media screen and (max-width: 768px) {
    body {
        font-size: 12px;
        line-height: 26px;
    }

    h2 {
        font-size: 30px;
    }

    .navbar {
        &.fixed-top {
            position: relative;
        }
    }
    .prcinfo {
        font-size: 14px;
    }

    .hero-banner {
        .hero-content {
            h1 {
                font-size: 32px;
            }
        }
    }

    .section-item-padding {
        padding-bottom: 60px;
    }
}
