html, body {
	height: 100%;
	margin: 0;
	padding: 0;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  height: 100%;
  font-size: 62.5%;
}

body {
    font-family: 'Encode Sans Expanded', serif;
    font-size: 16px;
    line-height: 30px;
    background: $color-white;
    color: $body-color;
    letter-spacing: normal;
    font-weight: 400;

    .page {
        background: #F7F7F7;
    }
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Encode Sans Expanded', serif;
    font-weight: 700;
}

h1 {
    font-size: 44px;
}

h2 {
    font-size: 38px;
}

h3 {
  font-size: 26px;;
}


a {
    color: $base-link;
}

.section-item-padding {
    padding-bottom: 120px;
}


.hero-banner {
    .row {
        min-height: 100vh;
    }

    .hero-content {
        width: 80%;
        margin: 0 auto;

        h1 {
            color: $base-color;
            margin-bottom: 30px;
        }

        p {
            margin-bottom: 40px;
        }

        a {
            background-color: $color-tosca;
            color: $base-color;
            border-radius: 50px;
            padding: 10px 20px;
            font-weight: 500;

            &:hover {
                cursor: pointer;
            }
        }
    }
}

.section-title,
.section-item-title {
    color: $base-color;
    margin-bottom: 20px;
}

.features {
    > .container {
        border-top: 1px solid #eaeaea;
        padding-top: 40px;
    }

    .featureitem {
        padding-bottom: 120px;
        position: relative;

        // &.order-management {
        //     &:after {
        //         position: absolute;
        //         content: '';
        //         right: -5%;
        //         top: 20%;
        //         background: url(../img/polygon7.png) no-repeat;
        //         background-position: bottom right;
        //         width: 50%;
        //         height: 50%;
        //         z-index: -1;
        //         background-size: contain;
        //     }
        // }

        &.auto-store {
            &:after {
                position: absolute;
                content: '';
                left: -23%;
                top: 25%;
                background: url(../img/Ellipse81.png) no-repeat;
                background-position: bottom left;
                width: 50%;
                height: 50%;
                z-index: -1;
                background-size: contain;
            }
        }

        &.invenmanage {
            &:after {
                position: absolute;
                content: '';
                right: -10%;
                top: 25%;
                background: url(../img/Polygon8.png) no-repeat;
                background-position: bottom right;
                width: 60%;
                height: 60%;
                z-index: -1;
                background-size: contain;
            }
        }

        &.store-integrated {
            &:after {
                position: absolute;
                content: '';
                left: -12%;
                top: 25%;
                background: url(../img/Rectangle621.png) no-repeat;
                background-position: bottom left;
                width: 40%;
                height: 40%;
                z-index: -1;
                background-size: contain;
            }
        }
    }
}

.pricing {
    > .container {
        border-top: 1px solid #eaeaea;
        padding-top: 40px;
    }

    .pricebox {
        text-align: center;
        padding: 20px;
        border-radius: 20px;
        min-height: 410px;
        margin-bottom: 15px;
    
        ul {
            list-style: none;
            padding-left: 0px;
        }
    
        h5 {
            margin-bottom: 20px;
            font-weight: 600;
        }
    
        .prcheading {
            margin-bottom: 20px;
            min-height: 54px;
    
            h3 {
                font-weight: 600;
            }
        }
    
        &.starter {
            background-color: $base-color;
            color: $color-white;
    
            h5 {
                color: $color-tosca;
            }
    
            h3 {
                color: $color-white;
                text-transform: uppercase;
            }
        }
    
        &.business {
            background-color: $color-tosca;
    
            h5 {
                color: $base-color;
            }
        }
    
        &.enterprise {
            background-color: $color-white;
            border: 2px solid $base-color;
    
            h5 {
                color: $color-tosca;
            }
    
            h3 {
                color: $base-color;
            }
        }
    }
    
    .priceaction {
        text-align: center;
        margin-bottom: 30px;
    
        p {
            margin-bottom: 0;
            display: inline-block;
            padding: 10px 20px;
            border-radius: 25px;
        }
    
        &.current {
            p {
                border: 1px solid $base-color;
                background-color: $color-white;
            }
        }
    
        &.base {
            p {
                border: 1px solid $base-color;
                background-color: $base-color;
                color: $color-white;
            }
        }
    }
}

footer {
    background-color: $base-color;
    color: $color-white;

    .footer-wrapper {
        padding: 50px 0;

        .widget-imgfoot {
            margin-bottom: 30px;

            img {
                width: 180px;
            }
        }

        .footer-social {
            padding-left: 0px;
            margin-top: 30px;

            li {
                list-style: none;
                padding-left: 0;
                display: inline-block;
                margin-right: 10px;

                a {
                    display: block;
                    width: 30px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    border-radius: 50%;
                    background-color: $color-white;
                    color: $base-color;
                    font-size: 14px;
                    -webkit-transition: all all 0.5s ease-out 0s;
                    -moz-transition: all all 0.5s ease-out 0s;
                    -ms-transition: all all 0.5s ease-out 0s;
                    -o-transition: all all 0.5s ease-out 0s;
                    transition: all all 0.5s ease-out 0s;

                    &:hover {
                        background-color: $color-tosca;
                    }
                }
            }
        }

        .widget-copyright {
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-end;
            align-items: flex-end;
            height: 100%;
        }
    }
}


